import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

import "./LinksDrawer.css";
const LinksDrawer = (props) => {
  return (
    <ul className={`links-drawer ${props.className}`}>
      {props.links.map((link) => (
        <li key={link}>
          {link === "deconnexion" ? (
            <Link onClick={props.deconnection} to="/">
              {link.charAt(0).toUpperCase() + link.slice(1)}
            </Link>
          ) : (
            <Link to={`/${link}`}>
              {link.charAt(0).toUpperCase() + link.slice(1)}
            </Link>
          )}
        </li>
      ))}
    </ul>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    deconnection: () => dispatch({ type: "DECONNECTION" }),
  };
};

export default connect(null, mapDispatchToProps)(LinksDrawer);
