import * as actionTypes from "./actions";

const initialState = {
  is_connecte: false,
  is_abonne: false,
  token: "",
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.CONNECTION:
      // Save local storage!
      return {
        ...state,
        is_connecte: true,
        token: action.token,
      };

    case actionTypes.DECONNECTION:
      // Remove local storage!
      return {
        ...state,
        is_connecte: false,
        is_abonne: false,
      };

    case actionTypes.ABONNEMENT:
      return {
        ...state,
        is_abonne: true,
        is_connecte: true,
        token: action.token,
      };

    case actionTypes.DESABONNEMENT:
      // Remove and Save new local storage
      return {
        ...state,
        is_abonne: false,
      };
    default:
      return state;
  }
};

export default reducer;
